import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { BrowserRouter, Route, Link } from 'react-router-dom'
import Premier from "./Premier";
import LaLiga from "./LaLiga";
import Bundes from './Bundes';


const useStyles = makeStyles(() => ({
  div: {
    textAlign: "center",
    width: "100%",
    display: "block"
  },
  formControl: {
    minWidth: 120,
    marginBottom: "1rem"
  },
  inputLabel: {
    fontSize: "1.3rem"
  },
  menuItem: {
    fontSize: "1.2rem"
  }
}));

export default function SelectLeague() {
  const classes = useStyles();
  const [standings, setStandings] = React.useState('');
  const [open, setOpen] = React.useState(false);

  const handleChange = (event) => {
    setStandings(event.target.value);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  return (  
    <BrowserRouter>
        <div className={classes.div}>
          <FormControl className={classes.formControl}>
            <InputLabel className={classes.inputLabel} id="demo-controlled-open-select-label">国から選ぶ</InputLabel>
            <Select
              labelId="demo-controlled-open-select-label"
              id="demo-controlled-open-select"
              open={open}
              onClose={handleClose}
              onOpen={handleOpen}
              value={standings}
              onChange={handleChange}
            >
              <MenuItem value="順位表">
                <em>選択なし</em>
              </MenuItem>
              <MenuItem className={classes.menuItem} value="プレミアリーグ"><Link to="/Premier">イングランド</Link></MenuItem>
              <MenuItem className={classes.menuItem} value="ラ・リーガ"><Link to="/LaLiga">スペイン</Link></MenuItem>
              <MenuItem className={classes.menuItem} value="ブンデスリーガ"><Link to="/Bundes">ドイツ</Link></MenuItem>
            </Select>
          </FormControl>    
      {/* <Switch> */}
        <Route exact path="/Premier" component={Premier}/>
        <Route path="/LaLiga" component={LaLiga}/>
        <Route path="/Bundes" component={Bundes}/>
      {/* </Switch> */}
      </div>
    </BrowserRouter>
  );
}
