import React, { Component } from 'react';
import "../App.css";

export default class BundesLiga extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tables: [],
        };
    }
    componentDidMount() {
    fetch("https://api.football-data.org/v2/competitions/BL1/standings?standingType=TOTAL",{
        headers: { 'X-Auth-Token': 'bb7cedb28741483b8a88e9b3de284f69' }
    })
      .then(res => res.json())
      .then((result) => {
        this.setState({
            tables: result.standings[0].table
        });
      })
  }

    render() {
        return (
                <table>
                    <thead>
                        <tr>
                            <th width="10%">順位</th>
                            <th width="40%">クラブ</th>
                            <th width="10%">試合</th>
                            <th width="10%">勝</th>
                            <th width="10%">分</th>
                            <th width="10%">負</th>
                            <th width="10%">Pts</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.state.tables.map((table) => (
                            <tr style={{textAlign: "center"}}>
                                <td>{table.position}</td>
                                <td className="team"><div className="teamImg"><img src={table.team.crestUrl}/></div><div className="teamName">{table.team.name}</div></td>
                                <td>{table.playedGames}</td>
                                <td>{table.won}</td>
                                <td>{table.draw}</td>
                                <td>{table.lost}</td>
                                <td>{table.points}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
        )
    }
}
