import React, { Component } from 'react'
import { BrowserRouter, Route, Link,Switch } from 'react-router-dom'
import BundesLigaStandings from "./BundesLigaStandings";
import BundesLigaSchedule from "./BundesLigaSchedule";
import BundesLigaTop from "./BundesLigaTop";
import "../App.css";


export default class Bundes extends Component {
    render() {
        return (
            <BrowserRouter>
                    <ul className="ul">
                        <li className="li"><Link to="/BundesLigaStandings" className="Link">順位表</Link></li>
                        <li className="li"><Link to="/BundesLigaSchedule" className="Link">日程</Link></li>
                        <li className="li"><Link to="/BundesLigaTop" className="Link">得点ランキング</Link></li>
                    </ul>
                <Route exact path="/BundesLigaStandings" component={BundesLigaStandings}></Route>
                <Route path="/BundesLigaSchedule" component={BundesLigaSchedule}></Route>
                <Route path="/BundesLigaTop" component={BundesLigaTop}></Route>
            </BrowserRouter>
        )
    }
}
