import React, { Component } from 'react'
import { BrowserRouter, Route, Link,Switch } from 'react-router-dom'
import Standings from "./Standings";
import Schedule from "./Schedule";
import TopRanker from "./TopRanker";
import "../App.css";


export default class Premir extends Component {
    render() {
        return (
            <BrowserRouter>
                    <ul className="ul">
                        <li className="li"><Link to="/Standings" className="Link">順位表</Link></li>
                        <li className="li"><Link to="/Schedule" className="Link">日程</Link></li>
                        <li className="li"><Link to="/Topranker" className="Link">得点ランキング</Link></li>
                    </ul>
                <Route exact path="/Standings" component={Standings}></Route>
                <Route path="/Schedule" component={Schedule}></Route>
                <Route path="/Topranker" component={TopRanker}></Route>
            </BrowserRouter>
        )
    }
}
