import React, { Component } from 'react'
import { BrowserRouter, Route, Link,Switch } from 'react-router-dom'
import LaLigaStandings from "./LaligaStandings";
import LaLigaSchedule from "./LaLigaSchedule";
import LaLigaTopranker from "./LaLigaTop";
import "../App.css";

export default class Spain extends Component {
    render() {
        return (
            <BrowserRouter>
                    <ul className="ul">
                        <li className="li"><Link to="/LaLigaStandings" className="Link">順位表</Link></li>
                        <li className="li"><Link to="/LaLigaSchedule" className="Link">日程</Link></li>
                        <li className="li"><Link to="/LaLigaTopranker" className="Link">得点ランキング</Link></li>
                    </ul>
                <Route exact path="/LaLigaStandings" component={LaLigaStandings}></Route>
                <Route path="/LaLigaSchedule" component={LaLigaSchedule}></Route>
                <Route path="/LaLigaTopranker" component={LaLigaTopranker}></Route>
            </BrowserRouter>
        )
    }
}
