import React, { Component } from 'react'

export default class BundesLigaSchedule extends Component {
    constructor(props) {
        super(props);
        this.state = {
            matches: []
        };
    }
    componentDidMount() {
    fetch("https://api.football-data.org/v2/competitions/BL1/matches?status=SCHEDULED",{
        headers: { 'X-Auth-Token': 'bb7cedb28741483b8a88e9b3de284f69' }
    })
      .then(res => res.json())
      .then((result) => {
        this.setState({
            matches: result.matches
        });
        let date= new Date(this.state.matches[0].utcDate);
        let jpdate = date.toLocaleString("ja-JP");
        let jdate = new Date(jpdate);
        let jtime = jdate.getMonth()+1+"月"+jdate.getDate()+"日"+jdate.getHours()+ ":" +("0"+jdate.getMinutes()).slice(-2);
        // console.log(jtime);
        console.log(this.state.matches[0])
        // let date= new Date(this.state.matches[i].utcDate);
        // let jpdate = date.toLocaleString("ja-JP");
        // let jdate = new Date(jpdate);
        // let jtime = jdate.getMonth()+1+"月"+jdate.getDate()+"日"+jdate.getHours()+ ":" +("0"+jdate.getMinutes()).slice(-2);
        // console.log(jtime);
      })

  }
    render() {
        return (
            <div className="scroll-bar">
                <table>
                    <thead>
                        <tr style={{textAlign: "center"}}>
                            <th width="40%">ホーム</th>
                            <th width="10%">日程</th>
                            <th width="40%">アウェイ</th>
                        </tr>
                    </thead>
                    <tbody id="matchday">
                        {this.state.matches.map((match) => (
                            <tr style={{textAlign: "center"}}>
                                <td>{match.homeTeam.name}</td>
                                <td>{match.utcDate}</td>
                                <td>{match.awayTeam.name}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        )
    }
}
