import React, { Component } from 'react'
import "../App.css"

export default class TopRanker extends Component {
    constructor(props) {
        super(props);
        this.state = {
            scorers: []
        };
    }
    componentDidMount() {
    fetch("https://api.football-data.org/v2/competitions/PL/scorers",{
        headers: { 'X-Auth-Token': 'bb7cedb28741483b8a88e9b3de284f69' }
    })
      .then(res => res.json())
      .then((result) => {
        this.setState({
            scorers: result.scorers
        });
      })
  }
    render() {
        return (
                <table>
                    <thead>
                        <tr style={{textAlign: "center"}}>
                            <th width="40%">選手名</th>
                            <th width="10%">得点</th>
                            <th width="40%">クラブ名</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.state.scorers.map((score) => (
                            <tr style={{textAlign: "center"}}>
                                <td>{score.player.name}</td>
                                <td>{score.numberOfGoals}</td>
                                <td style={{textAlign: "center"}}>{score.team.name}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
        )
    }
}
